/* Ensuring the table layout is not disrupted */
table {
  width: 100%;
  border-collapse: collapse;
}

thead {
  background-color: #f2f2f2;
}

th,
td {
  padding: 10px;
  text-align: center;
  border: 1px solid #ddd;
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}

/* Add a green background for Sunday columns */
th.sunday,
td.sunday {
  background-color: #90EE90;
  /* Light green background */
}

.add-button {
  margin: 20px;
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  width: 200px;
  height: 50px;
  font-size: 20px;
}

.save-button {
  font-size: 20px;
  width: 150px;
  height: 50px;
  margin: 20px;
  padding: 10px 20px;
  background-color: #0036cc;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.add-button:hover {
  background-color: #45a049;
}

/* Popup styling */
.popup-container {
  position: fixed;
  /* Fixed to the screen */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  /* Semi-transparent overlay */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  /* Ensure it appears above everything */
}

.popup-form {
  background-color: white;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  width: 400px;
}

.popup-form h2 {
  text-align: center;
  margin-bottom: 20px;
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 300px;
  text-align: center;
}

.popup-actions {
  margin-top: 20px;
}

.popup-actions button {
  margin: 0 5px;
  padding: 8px 16px;
}

/* Popup container */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Popup content */
.popup-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Popup header */
.popup-content h3 {
  margin-bottom: 20px;
  font-size: 18px;
  text-align: center;
}

/* Form labels and inputs */
.popup-content label {
  font-size: 14px;
  margin-bottom: 8px;
  text-align: left;
  width: 100%;
}

.popup-content input {
  width: 100%;
  padding: 8px;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}

.popup-content .buttons {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

/* Button styling */
.popup-content button {
  padding: 8px 16px;
  border: none;
  background-color: #4CAF50;
  color: white;
  font-size: 14px;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.popup-content button:hover {
  background-color: #45a049;
}

.popup-content button[type="button"] {
  background-color: #f44336;
}

.popup-content button[type="button"]:hover {
  background-color: #d32f2f;
}

/* Close popup button */
.popup-content button[type="button"]:nth-child(3) {
  background-color: #888;
}

.popup-content button[type="button"]:nth-child(3):hover {
  background-color: #666;
}


.popup-form input {
  width: 95%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.popup-form button {
  width: 90%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.popup-form button:hover {
  background-color: #0056b3;
}

.close-popup {
  margin-top: 15px;
  padding: 10px;
  background-color: #ec0000;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 25px;
}

@media (max-width: 768px) {
  table {
    width: 100%;
    font-size: 12px;
  }

  th,
  td {
    padding: 5px;
  }

  .add-button,
  .save-button,
  .download-button {
    width: 100%;
  }
}


