.notification {
  position: fixed;
  top: 10px;
  right: 10px;
  background-color: green;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  z-index: 1000;
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

.notification p {
  margin: 0;
}

.notification.fade-out {
  opacity: 0;
}


.input-field {
  width: 40px;
  /* Make the input field square */
  height: 40px;
  /* Make the input field square */
  text-align: center;
  /* Center the text */
  font-size: 16px;
  /* Adjust font size */
  border: 1px solid #ccc;
  /* Add a border */
  border-radius: 4px;
  /* Optional: add rounded corners */
  margin: 0;
}

.input-field:focus {
  outline: none;
  border-color: #4CAF50;
  /* Green border on focus */
}


.input-field::placeholder {
  color: #999;
}